@mixin flex-between-center {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@mixin flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin position-absolute($top, $left, $index) {
  position: absolute;
  top: $top;
  left: $left;
  z-index: $index;
}
